import Vue from 'vue'
import Router from 'uni-simple-router';
import index from '@/pages/components/index.vue'
import goods from '@/pages/components/Goods/Goods.vue'
import rattings from '@/pages/components/Rattings/Rattings.vue'
import sellers from '@/pages/components/Sellers/Sellers.vue'

Vue.use(Router)

const routes = [
  {
    path: '/',
    // aliasPath:'/', // 
    name: 'index',
    component: index,
	children: [{
		path:'/sellers',
		name: 'sellers',
		component: sellers
	  },
	  {
		path:'/goods',
		name: 'goods',
		component: goods
	  },
	  {
		path:'/rattings',
		name: 'rattings',
		component: rattings
	  }]
  }
  ]

const router = new Router({
	h5:{
    vueRouterDev:true,  //vue-router  false  
  },
  routes
  //mode: 'history'
})

//
router.beforeEach((to, from, next) => {
  next()
})
// 
router.afterEach((to, from) => {
})

router.push('/goods')

export default router